class About extends React.Component {

  constructor() {
    super();
    this.state = { width: 0, height: 0, images: IMAGES };
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }
  
  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  render(){
    this.state.images.forEach(function (image) {
      image.thumbnailWidth = "500px";
      image.thumbnailHeight = "400px";
    });
    return(
      <div className='App'>
      <Header />
      <div className='Center'>
        <img src={require("../Assets/livinglogo.png")} className="LogoHome" />
        <h1 className="LivingHome">LIVING MEMORIES</h1>
        <h3 className="ServiceHome">Funeral Services</h3>
        <hr className="GoldenLine"></hr>
        <div>
        <h1 className="HomeHeading">Gallery</h1>
        <div style={{ display: "flex",
        flexDirection: "column",
        flexWrap: "wrap",
        marginTop: "10px"}}>
         <div style={{
                    alignSelf: "center",
                    minHeight: "1px",
                    marginLeft: "2px",
                    marginRight: "2px",
  
                    width: "98vw",
                    }}>
                <Gallery
            images={IMAGES}
            enableLightbox={true}
            enableImageSelection={false}/>
                </div>
                
        </div>
       
        </div>
      </div> 
        <Footer></Footer>
    </div>
    )
  }
}

export default About;
